<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">行业研报</div>
      </div>
      <div class="list-contaienr">
        <!-- <div class="list-nav">
          <span @click="goBack('tuneUpAssistantList')">尽调助手&nbsp;&nbsp;></span>
          <span>热门行业研报</span>
        </div> -->
        <!-- <div class="list-line"></div> -->
        <div class="title_name"><span>行业选择</span></div>
        <div class="hy_items_box">
            <div class="hy_item_box" v-for="(item,index) of dataList" :key="index" @click="toListPage( item.id,item.name)" >
                <img :src="item.icon" alt="">
                <div class="item_shadow_box">
                    <span>{{item.name}}</span>
                </div>
            </div>
        </div>
      </div> 
    </div>
  </div>

</template>

<script>
	import {
		hotReport
	} from "../../config/api";
	import {
		dialogConfirm,
		dialogConfirmHasbtn
	} from "../../config/mUtils";
	export default {
		data() {
			return {
				currentPage: 1,
				keyWord: "",
				pageSize: 10,
				total: '',
				totalTxt: '',
				dataList: []
			};
		},
		mounted() {
			//   this.params = this.$route.query;
			this.getArticleDetails();
		},
		methods: {
			//获取详情信息
			//   currentChange(val) {
			//     if (this.total > this.pageSize) {
			//       this.getArticleDetails(val);
			//     }
			//   },
			getArticleDetails() {
				hotReport().then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.dataList = res.data.payload;
						// console.log(this.dataList )
					} else {
						dialogConfirm("info", res.data.message);
					}
				});
			},
			goBack(name) {
				this.$router.push({
					name: name
				});
			},
			toListPage(id, name1) {
				this.$router.push({
					name: 'jdzsHomepageRmybList',
					query: {
						category: id,
						name: name1
					}
				})
			},
		}
	};
</script>

<style lang="scss" scoped>
	.content {
		width: 100%;
		min-height: 100%;
		background-color: white;
		padding: 20px 30px;
		// padding-top: 30px;
	}
	
	.hidden {
		display: none;
	}
	
	.content-title>div.left {
		font-size: 18px;
		line-height: 30px;
		color: #35383a;
		font-weight: bold;
	}
	
	.header-right-time {
		margin-right: 10px; // position: relative;
	}
	
	.list-contaienr {
		// border: 1px solid #e4e4e4;
		// padding: 20px 17px 50px 17px;
		position: relative;
	}
	
	.list-nav {
		// padding-left: 14px;
		margin-bottom: 12px;
	}
	
	.list-nav span {
		font-size: 14px;
		color: #3c98e8;
		cursor: pointer;
	}
	
	.list-nav span:last-child {
		color: #35383a;
		cursor: inherit;
	}
	
	.list-line {
		border-bottom: 1px solid #f4f4f4;
	}
	
	.title_name {
		margin: 0px 0px 15px 0px;
		//   padding-left: 5px;
		font-size: 18px;
		color: #35383A;
	}
	
	.hy_items_box {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		.hy_item_box {
			position: relative;
			margin: 0px 15px 20px 0px;
			border-radius: 8px;
			width: 160px;
			height: 103px;
			overflow: hidden;
			img {
				width: 160px;
				height: 103px;
				border-radius: 8px;
				transition: all 0.3s linear;
			}
			&:hover {
				// box-shadow: 0px 1px 10px 0px rgba(90, 143, 202, 0.3);
				// cursor: pointer;
				// transform: translateY(-10px)
				img {
					transform: scale(1.3, 1.3);
				}
			}
			.item_shadow_box {
				width: 160px;
				height: 103px;
				position: absolute;
				top: 0px;
				left: 0px;
				padding: 0 10px;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				font-size: 16px;
				cursor: pointer;
				border-radius: 8px;
				span {
					color: #FFFFFF;
				}
			}
		}
	}
</style>


